var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"curso-main-container pb-3"},[_c('BannerInterno'),_c('div',{staticClass:"container tarjeta tarjeta--blanca p-4 p-md-5"},[_vm._m(0),_c('p',{staticClass:"mb-5"},[_vm._v("Para iniciar el estudio del marketing digital, es necesario hablar del marketing convencional u offline, el cual se ha aplicado siempre en las empresas y hoy en día trasciende a un ámbito y un entorno online. Se debe tener en cuenta que el marketing digital tiene una estructura muy similar al convencional; el proceso es igual, las estrategias van dirigidas a los mismos clientes y consumidores. La principal diferencia radica en los medios utilizados para el desarrollo de las estrategias de venta, distribución, promoción e investigación. ")]),_vm._m(1),_vm._m(2),_c('p',{staticClass:"mb-5"},[_vm._v("Existen varios conceptos que pueden definir el mercado, cada uno de ellos con variables muy particulares en relación con el tema; sin embargo, con puntos en común.")]),_vm._m(3),_c('p',{staticClass:"mb-5"},[_vm._v("Todas ellas tienen en común el enfoque en la producción y comercialización de bienes y servicios; sin embargo, el marketing va más allá de eso. En la definición de Kotler se puede observar cómo hace énfasis en lo social, aludiendo claramente que el mercadeo tiene un enfoque en el ser; es decir, en el consumidor. La AMA por su parte enfatiza en la propuesta de valor, elemento fundamental en el proceso de mercadeo, aunque el aspecto más importante a resaltar es la “satisfacción de necesidades”. Se podría decir entonces que el marketing, mercadeo o mercadotecnia es un conjunto de actividades que se encargan del estudio de un mercado, con el fin de identificar necesidades, gustos o deseos para satisfacerlos a través de bienes o servicios que den al cliente beneficios que obtienen utilidades para la empresa.")]),_vm._m(4),_c('p',{staticClass:"mb-5"},[_vm._v("El marketing está presente en muchos escenarios de la vida de los seres humanos: la publicidad, las ventas, las promociones y ofertas, la comercialización, la canasta familiar. En un sistema capitalista que basa su economía en la producción y el consumo, la mercadotecnia es parte fundamental de la vida y es indispensable para el desarrollo laboral, personal y social. El marketing permite que las marcas anuncien sus productos y que los clientes reales o potenciales se enteren de su existencia, en ese orden de ideas no solo es un proceso de comercialización sino también de información comercial. ")]),_c('br'),_c('p',{staticClass:"mb_5"},[_vm._v("Para algunos expertos el mercadeo tiene como función principal la producción y comercialización de bienes y servicios en torno a la satisfacción de necesidades. Para quienes ven el mercadeo de forma estratégica su principal función radica en el conocimiento profundo y absoluto de los consumidores: gustos, deseos, estilo de vida, intereses, pensamiento, comportamiento y actitud frente a la compra y consumo. Desde un punto de vista más holístico el marketing cumple con todas esas funciones o debería hacerlo; es decir, una empresa que implemente un proceso de mercadeo integral debe comenzar con el análisis del mercado para lograr un absoluto conocimiento de sus clientes y consumidores, pasando por el desarrollo de productos y servicios con propuestas de valor claras y con beneficios sólidos hasta el desarrollo de todo el proceso de comunicación con sus clientes y consumidores.")]),_c('br'),_vm._m(5),_c('p',{staticClass:"mb-5"},[_vm._v("Las variables del marketing se han estudiado anteriormente; sin embargo se listarán, para tener un panorama nítido de ellas y de los elementos que el mercadeo debe tener claro para el desarrollo de las estrategias: ")]),_c('h5',{staticClass:"mb-4"},[_vm._v("Variables externas")]),_vm._m(6),_c('h5',{staticClass:"mb-4"},[_vm._v("Variables internas")]),_c('p',{staticClass:"mb-5"},[_vm._v("También llamadas variables controlables; estas se encuentran bajo el dominio y manejo de la empresa y permiten desarrollar estrategias enfocadas en la satisfacción total de los clientes y consumidores, generando atracción hacia el producto, servicio o marca. Las variables controlables también permiten contrarrestar los efectos de las no controlables o externas; por ejemplo, con la pandemia del año 2020 con la llegada de la COVID-19, varias empresas debieron realizar estrategias de mercadeo para contrarrestar los efectos que el confinamiento y la cuarentena les produjo desde el punto de vista económico, fue así como muchas empresas debieron implementar estrategias de distribución de sus productos basadas en las entregas a domicilio, otras debieron crear nuevos portafolios de productos o servicios que se adaptaran más a un nuevo sistema de comercialización o satisficiera otras necesidades de sus clientes. Por su puesto, las empresas deberían tener planes de contingencia para este tipo de eventos, no obstante, la pandemia del COVID-19 tomó por sorpresa, incluso a las más grandes y organizadas. ")]),_vm._m(7),_vm._m(8),_vm._m(9),_c('p',{staticClass:"mb-5"},[_vm._v("El marketing tiene un proceso definido que permite a las empresas el desarrollo de estrategias oportunas para la satisfacción de las necesidades de los consumidores. Este proceso debería ser invariable toda vez que seguirlo permite llegar a resultados más contundentes, se disminuye el riesgo o se hacen ajustes más oportunos. A continuación, se indican los pasos a seguir en el proceso de mercadeo:")]),_vm._m(10),_vm._m(11),_vm._m(12),_vm._m(13),_vm._m(14),_c('p',{staticClass:"mb-5"},[_vm._v("Como ya se mencionó anteriormente, la razón principal por la que un cliente compra o prefiere un producto por encima de otro de la competencia, es la propuesta de valor; es decir el beneficio que ese producto o servicio le proporcione al cliente o consumidor. Vale recordar en este momento que el precio del producto debe ser menor al beneficio percibido por el cliente. ")]),_vm._m(15),_c('div',{staticClass:"h4"},[_vm._v("Proceso de creación de valor")]),_vm._m(16),_c('p',[_vm._v("Como se puede observar en la figura, la creación de valor comienza por la necesidad, los deseos o los gustos presentes en el mercado, para ello es necesario que quien realice la investigación y el análisis sea muy hábil en identificar dichos aspectos del mercado, pues de este punto depende la efectividad del proceso; de allí debe partir el diseño de las estrategias y de la propuesta de valor, la cual debe ser una única propuesta acompañada si se quiere de otros factores diferenciadores o de ventajas que tenga el producto. Sin embargo, una excelente propuesta de valor no es suficiente para el desarrollo del proceso y debe considerarse la comunicación interactiva entre la marca y el cliente, con el fin de informar de manera efectiva y atractiva dicha propuesta a los consumidores reales y potenciales; todo esto debe redundar en la captación de valor por parte del cliente y la generación de utilidades para la empresa.")]),_c('div',{staticClass:"hr-blank"}),_vm._m(17),_vm._m(18),_c('div',{staticClass:"h4"},[_vm._v("Tipos de mercado")]),_c('p',{staticClass:"mb-5"},[_vm._v("Cada producto o servicio es un mundo aparte y muy particular, para cada uno existen consumidores reales y potenciales con características bien definidas. Es por ello, que existe también una variedad importante de mercados:")]),_c('div',{staticClass:"mx-3"},[_c('AcordionA',{staticClass:"mb-5",attrs:{"tipo":"b","clase-tarjeta":"tarjeta t-gris"}},[_c('div',{staticClass:"row",attrs:{"titulo":"Mercado de consumo"}},[_c('div',{staticClass:"col-lg justify-content-md-center"},[_c('p',[_vm._v("Es el mercado masivo, aquel que tiene necesidades permanentes y debe satisfacerlas para su supervivencia y bienestar o satisfacción personal. Dentro de él existen dos variaciones de acuerdo con el tipo de producto o servicio:")]),_c('div',{staticClass:"ul lista-ul--color mb-5"},[_c('li',[_c('i',{staticCla